import Rete from 'rete'

import SocketName from '../../const/socket'
import ComponentName from '../../const/component'
import NewPostbackTemplate from './NewPostbackTemplate'

export default class NewPostbackComponent extends Rete.Component {
  constructor (config) {
    super(ComponentName.POSTBACK_NEW)

    this.data.component = NewPostbackTemplate
    this.socket = config ? config.sockets : null
  }

  builder (node) {
    this.setConnection()

    const triggerInput = new Rete.Input(
      SocketName.NEW_POSTBACK_INPUT_TRIGGER, 
      'Trigger', 
      this.socket.newPostbackInputTrigger, 
      false
    )
    
    const newPostbackInput = new Rete.Input(
      SocketName.NEW_POSTBACK_INPUT, 
      'Reply', 
      this.socket.newPostbackInput, 
      true
    )
    
    const newPostbackOutput = new Rete.Output(
      SocketName.NEW_POSTBACK_OUTPUT, 
      'Next', 
      this.socket.newPostbackOutput,
      false
    )

    const newPostbackOutputSequence = new Rete.Output(
      SocketName.NEW_POSTBACK_OUTPUT_SEQUENCE, 
      'Sequence', 
      this.socket.newPostbackOutputSequence,
      false
    )

    return node
      .addInput(triggerInput)
      .addInput(newPostbackInput)
      .addOutput(newPostbackOutput)
      .addOutput(newPostbackOutputSequence)
  }

  // eslint-disable-next-line
  worker (node, inputs, outputs) {
    outputs[SocketName.NEW_POSTBACK_OUTPUT] = node.data
  }

  setConnection() {
    // NewPostback may take connections from:
    this.socket.newPostbackInput.combineWith(this.socket.quickReplyOutput)
    this.socket.newPostbackInput.combineWith(this.socket.buttonOutput)

    // NewPostback may be connected to:
    this.socket.newPostbackOutput.combineWith(this.socket.textInput)
    this.socket.newPostbackOutput.combineWith(this.socket.carouselInput)
    this.socket.newPostbackOutput.combineWith(this.socket.facebookMediaInput)
    this.socket.newPostbackOutput.combineWith(this.socket.imageInput)
    this.socket.newPostbackOutput.combineWith(this.socket.videoInput)
    this.socket.newPostbackOutput.combineWith(this.socket.audioInput)
    this.socket.newPostbackOutput.combineWith(this.socket.fileInput)
    this.socket.newPostbackOutput.combineWith(this.socket.ecommerceInput)
    this.socket.newPostbackOutput.combineWith(this.socket.userInputFlowInput)
    this.socket.newPostbackOutput.combineWith(this.socket.genericTemplateInput)
    this.socket.newPostbackOutput.combineWith(this.socket.otnInput)

    // Input: Triggers
    this.socket.newPostbackInputTrigger.combineWith(this.socket.triggerOutput)    

    // Output: Sequence
    this.socket.newPostbackOutputSequence.combineWith(this.socket.sequenceInput)    
  }  
}