import Swal from 'sweetalert2'

import { sweetAlertCommonParams } from '../../functions/utils'

export const newPostbackModal = async (oldData, node) => {
  const commonParams = sweetAlertCommonParams(node)
  const isHidable = node.newPostbackModalHidableFieldsStatus || false
  const hidableClass = isHidable ? 'd-none' : ''

  return await Swal.fire({
    ...commonParams,
    title: 'Configure New Postback',
    html: 
      '<div class="form-group text-left mt-4">' +
        '<label for="new-postback-postback-title">Title</label>' +
        '<input type="text" id="new-postback-postback-title" class="form-control">' +
      '</div>' +
      
      '<div class="form-group text-left mt-2 ' + hidableClass + '">' +
        '<label for="new-postback-choose-label">Choose label(s)</label>' +
        '<select id="new-postback-choose-label" class="form-control select2 new-postback-hidable-field" multiple="">' +
        '</select>' +
      '</div>' +
      
      '<div class="form-group text-left mt-2 ' + hidableClass + '">' +
        '<label for="new-postback-choose-sequence">Choose sequence</label>' +
        '<select id="new-postback-choose-sequence" class="form-control select2 new-postback-hidable-field">' +
        '</select>' +
      '</div>',
    willOpen: () => {
      // Apply select2 to select
      $('#new-postback-choose-label, #new-postback-choose-sequence').select2({ width: '100%' })

      var page_table_id = window.xitFlowBuilderData.page_table_id
      var base_url = window.xitFlowBuilderData.base_url
      $.ajax({
        type: 'POST',
        url: base_url+'visual_flow_builder/get_label_sequence_dropdown',
        dataType: 'JSON',
        data: { page_table_id, requested_from: 'new_postback' },
        success:function(response) {
          $("#new-postback-choose-label").html(response.label_dropdown);
          $("#new-postback-choose-sequence").html(response.drip_dropdown);

          $('#new-postback-choose-label').val(oldData.labelIds || '').trigger('change')
          $('#new-postback-choose-sequence').val(oldData.sequenceIdValue || '').trigger('change')
        }
      })

      $('#new-postback-postback-title').val(oldData.title || '')
      $('#new-postback-choose-label').val(oldData.labelIds || '').trigger('change')
      $('#new-postback-choose-sequence').val(oldData.sequenceIdValue || '').trigger('change')

      $('#new-postback-choose-sequence').on('change', function() {
        Swal.resetValidationMessage()
        const currentValue = $(this).val()
        if ('newSequence' === oldData.sequenceIdValue && 'newSequence' !== currentValue) {
          node.newPostbackNodeNewSequenceValueChangeStatus = true;
          Swal.showValidationMessage('You are going to change the sequence value. If you do so, then the components, created by choosing "New sequence" previously, will be lost. If you want so, click on the "OK" button, otherwise, click on "Cancel" button.')
        }
      })
    },

    preConfirm: () => {
      const title = $('#new-postback-postback-title').val()

      const labelIds = $('#new-postback-choose-label').val()
      const labelIdTexts = $('#new-postback-choose-label option:selected')
      
      const sequenceIdValue = $('#new-postback-choose-sequence').val()
      const sequenceIdText = $('#new-postback-choose-sequence option:selected').text()
      
      if (! title) {
        Swal.showValidationMessage('Please provide a title.')
      }

      const labelIdTextsArray = []
      if (Array.isArray(labelIds) && labelIds.length > 0) {
        if (Object.keys(labelIdTexts).length > 0) {
          for (const text of labelIdTexts) {
            labelIdTextsArray.push(text.label)
          }
        }
      }
      
      return {
        title, 
        labelIds,
        labelIdTextsArray, 
        sequenceIdValue,
        sequenceIdText,
      }
    },
  })
}